import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContentComponent }      from './content/content.component';
import { ProductComponent }      from './product/product.component';
import {  BlankPageComponent }   from './blank-page/blank-page.component';
import { DepartmentComponent }   from './department/department.component';
import { CategoryComponent } from './category/category.component';
import { UnitComponent } from './unit/unit.component';
import { CustomerComponent} from './customer/customer.component';
import { PayComponent} from './pay/pay.component';
import { PrinterComponent} from './printer/printer.component';
import { ModifierComponent} from './modifier/modifier.component';
import { ServiceComponent} from './service/service.component';
import { VendorComponent} from './vendor/vendor.component';

const routes: Routes = [
  
  { path: '', component: ContentComponent },
  { path: 'product', component: ProductComponent },
  { path: 'blankpage', component: BlankPageComponent },
  { path:'dept', component: DepartmentComponent},
  { path: 'cat', component: CategoryComponent},
  { path: 'uni', component: UnitComponent},
{ path: 'vend' , component: VendorComponent},
{ path: 'cstmr' , component: CustomerComponent},
{ path: 'paymthd' , component: PayComponent},
{ path: 'prnter' , component: PrinterComponent},
{ path: 'srvice' , component: ServiceComponent},
{ path: 'modi' , component: ModifierComponent},




];

@NgModule({
  imports:[RouterModule.forRoot(routes)] ,
  exports:[RouterModule],
})

export class AppRoutingModule { }
